<template>
  <el-dialog
      :title="$t('user.bindEmailDialog.title')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="580px" class="mdphone-dialog">
      <el-form :model="phoneForm" :rules="phoneFormRules" ref="phoneForm" label-width="180px" >
        <el-form-item :label="$t('user.bindEmailDialog.email')" prop="user_account">
          <el-input v-model.trim="phoneForm.user_account" placeholder=""></el-input>
        </el-form-item>
        <el-form-item :label="$t('translateLang.user.text1')" prop="captcha" mes class="img-code-container">
          <el-input v-model.trim="phoneForm.captcha"></el-input>
          <img-code v-if="host.indexOf('hkcexp') > -1" url="https://crypto.hkcexp.com/api/Login/captchaVerify"></img-code>
          <img-code v-else url="http://192.169.1.210:7569/api/Login/captchaVerify"></img-code>
          <!-- <img v-if="host.indexOf('hkcexp') > -1" src="https://crypto.hkcexp.com/api/Login/captchaVerify" alt=""/> -->
          <!-- <img v-else src="http://192.169.1.210:7569/api/Login/captchaVerify" alt=""/> -->
        </el-form-item>
        <el-form-item :label="$t('user.bindEmailDialog.code')" prop="sms_verify" class="verify">
          <el-input type="text" auto-complete="off"  v-model.trim="phoneForm.sms_verify"></el-input>
          <el-button class="get-verify" @click="handleSendRequestSms($event)" :disabled="!validCode">{{ validTip }}</el-button>
        </el-form-item>
        <div class="btn-group appeal-btn">
          <el-button type="primary"  @click="phoneSubmit('phoneForm')">{{ $t('dialog.sure') }}</el-button>
        </div>
      </el-form>
    </el-dialog>
</template>

<script>
  import { sendRequestSms } from '@/api/loginModule'
  import { phoneORemailAuth } from '@/api/user'
  import { Email } from '@/utils/validate'
  import imgCode from '@/components/imgCode'
  export default {
    components: { imgCode },
    props: ['dialogVisible'],
    data() {
      const validateEmail = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutEmail')))
        } else {
          if (Email(value)) {
            callback()
          } else {
            callback(new Error(this.$t('errorTips.emailFormat')))
          }
        }
      }
      return {
        host: window.location.host,
        phoneForm: {
          sms_verify: '',
          user_account: '',
          require_type: 1
        },
        phoneFormRules: {
          sms_verify: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ],
          captcha: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ],
          user_account: [
            { required: true, validator: validateEmail, trigger: 'blur' }
          ]
        },
        validCode: true,
        validTip: this.$t('verifyCode.title')
      }
    },
    computed: {
    },
    created() {
  
    },
    methods: {
      handleCloseDialog() {
        this.$emit('closeDialog', 'bindEmailDialogVisible')
      },
      phoneSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.phoneForm }
            phoneORemailAuth(data).then(res => {
              console.log('res', res)
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('errorTips.bindEmailSuccess'))
              this.$emit('getUserInfo')
              this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      handleSendRequestSms(event) {
        if (!this.phoneForm.user_account) {
          this.$message.error(this.$t('errorTips.withoutEmail'))
          return false
        }
        if (!Email(this.phoneForm.user_account)) {
          this.$message.error(this.$t('errorTips.emailFormat'))
          return false
        }
        const data = {}
        this.setTime()
        data.user_account = this.phoneForm.user_account
        data.request_type = 'edit'
        sendRequestSms(data).then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
        }).catch(() => {
        })
      },
      setTime() {
        let time = 60
        if (this.validCode) {
          this.validCode = false
          const _this = this
          const t = setInterval(function() {
            time--
            _this.validTip = time + _this.$t('verifyCode.second')
            if (time === 0) {
              clearInterval(t)
              _this.validTip = _this.$t('verifyCode.getAgain')
              _this.validCode = true
            }
          }, 1000)
          return t
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .mdphone-dialog{
    .payway{
      margin-bottom: 15px;
      label{
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
    .tips{
      color: #b0b1b7;
      margin-left: 90px;
      margin-bottom: 10px;
    }
  }
</style>
<style lang="less">
  .mdphone-dialog{
    .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .imgcode{
      position: absolute;
      right: 74px;
      width: 100px;
      height: 35px;
      top: 2px;
    }
    /deep/.img-code-container{
      position: relative;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }

  }
</style>
<style lang="less">
  .mdphone-dialog{
    .verify .el-input__inner,
    .verify .el-input{
      width: 184px;
    }
    .get-verify {
      width: 95px;
      margin-left: 10px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(31,168,246,.15);
      color: rgba(31, 168, 246, 0.7);
    }
    .sure-change{
      padding: 12px 100px;
    }
  }
</style>


